.card {
  
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  transition: 0.3s;
  border-radius: 5px;
  background-color: rgb(182, 199, 255);
}


.card:hover {
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}
.containers {
  padding: 2px 16px;
}


.star{
  cursor: pointer;
  transition: color 200ms;
}
.maincontainer{
  position: relative;
  width:400px;
  height:400px;
  
 
}
.thecard{
  position:absolute; 
  height:100%;
  width: 100%;
  transform-style: preserve-3d;
  transition: all 0.5s ease;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  transition: 0.3s;
  border-radius: 5px;

}
.front{
  position:absolute;
  height:100%;
  width: 100%;
  backface-visibility: hidden;
  background: #ecad0131;
  color:rgb(202, 11, 11)
}
.back{
  position:absolute;
  height:100%;
  width: 100%;
  backface-visibility: hidden;
  background: pink;
  color:#333;
  transform:rotateY(180deg);
}
.thecard:hover{
  transform: rotateY(180deg);
}
